<template>
    <div class='coachlist studentSelectCourse'>
        <div class='top'>
          <div class='title'>课程总览 </div>
        </div>
        <div class="courseListWrap">
        	<div class="head">
        		<ul class="week">
        			<li class="weekItem" v-for="(item, index) in weekList" :key="index">{{item}}</li>
        		</ul>
        	</div>
        	<div class="left">
        		<div class="morning">
        			<div class="cellss1">
        				上午
        			</div>
        			<div class="cellss2">
        				<span>8:00~9:40</span>
        				<span>10:00~11:40</span>
        			</div>
        		</div>
        		<div class="morning">
        			<div class="cellss1">
        				下午
        			</div>
        			<div class="cellss2">
        				<span>14:00~15:40</span>
        				<span>16:00~17:40</span>
        			</div>
        		</div>
        	</div>
        	<div class="courseWrap">
        		<ul class="courseCell" v-for="(item,i) in courseArr" :key="i">
        			<li class="courseInfo" v-for="(cell, j) in item" :key="j" @click="selectClick(cell,i,j)">
        				<span style="margin-bottom: 0.1rem;">{{cell.courseName}}</span>
        				<span>{{cell.name}}</span>
        			</li>
        		</ul>
        	</div>
        	<div>{{flag}}</div>
        </div>
    </div>
</template>
<script>
import Vue from 'vue'
import Table from '../../components/Table'
import {addStudentCourse,queryScheduleCourse, upadteStudentCourse} from '../../services/service.js'
import {isApiResponseOk, setSessionStorage, getSessionStorage} from '../../utils/com.js'
export default {
  data () {
    return {
    	weekList: [' ', ' ', '周一', '周二', '周三', '周四', '周五', '周六', '周天'],
    	courseArr: [],
    	tempArr: [],
    	flag: ''
    }
  },
  components: {},
  created () {
  	let that = this
  	let professional = getSessionStorage('userInfo').professional
  	this.inData({professional: professional})
  },
  mounted () {
  },
  methods: {
  	async inData (data) {
  		let that = this
  		for (var i=0; i<4; i++) {
	  		that.courseArr[i] = new Array
	  		for (var j=0; j<7; j++) {
	  			that.courseArr[i][j] = {}
	  		}
	  	}
  		const res = await queryScheduleCourse(data)
  		if (res && res.code == 0) {
			let list = res.data.data
			for (var i=0; i<list.length; i++) {
				for (var key in list[i].course) {
					if (key == 'courseName') {
						list[i][key] = list[i].course[key]
					}
				}
				for (var key2 in list[i].teachers.users) {
					if (key2 == 'name') {
						list[i][key2] = list[i].teachers.users[key2]
					}
				}
				for (var key3 in list[i].teachers) {
					if (key3 == 'professionalName') {
						list[i][key3] = list[i].teachers[key3]
					}
				}
			}
			let tempArr = []
			for (var i=0; i<list.length; i++) {
				let obj = {}
				obj.week = list[i].week - 1
				obj.time = that.checkTime(list[i].time)
				obj.name = list[i].name
				obj.id = list[i].id
				obj.courseName = list[i].courseName
				tempArr.push(obj)
			}
			this.tempArr = tempArr
			for (var i=0; i<tempArr.length; i++) {
				that.courseArr[tempArr[i].time][tempArr[i].week] = tempArr[i]
				that.flag = ' '
			}
		} else {
			that.isPage = false
			that.$message({
				showClose: true,
				message: res.message,
				type: 'warning'
			})
		}
  	},
  	selectClick (item) {
  		let that = this
  		if (item.id) {
  			this.$confirm('确定选择该课程？', {type: 'warning'})
		        .then(_ => {
		        if (!that.$route.query.id) {
		        	addStudentCourse({selectCourseId: item.id}).then(function (res){
			      		if (res && res.code == 0) {
			      			that.$message({
			    				showClose: true,
								message: '选课成功',
								type: 'success'
			    			})
			    		} else {
			    			that.$message({
			    				showClose: true,
								message: res.message,
								type: 'warning'
			    			})
			    		}
			      	})
		        } else {
		        	let data = {
		        		OldSelectCourseId: that.$route.query.id,
		        		NewSelectCourseId: item.id
		        	}
		        	upadteStudentCourse(data).then(function (res){
			      		if (res && res.code == 0) {
			      			that.$message({
			    				showClose: true,
								message: '改选成功',
								type: 'success'
			    			})
			    		} else {
			    			that.$message({
			    				showClose: true,
								message: res.message,
								type: 'warning'
			    			})
			    		}
			      	})
		        }
		    })
		      .catch(_ => {})
  		}
  	},
  	checkTime (str) {
  		switch(str) {
  			case '8:00~9:40':
  			  	return 0;
		  	case '10:00~11:40':
		  		return 1;
		  	case '14:00~15:40':
		  		return 2;
		  	case '16:00~17:40':
		  		return 3;
  		}
  	}
  }
}
</script>
<style lang="sass" scoped>
.coachlist
  .top
    display: flex;
    height: .36rem;
    margin-bottom: .3rem;
    .btn
      font-family: PingFangSC-Medium;
      font-size: .15rem;
      color: #20C6BA;
      letter-spacing: 0;
      line-height: .15rem;
      height: .36rem;
      line-height: .36rem;
      border: 1px solid #20C6BA;
      border-radius: .02rem;
      width: 1.32rem;
      text-align: center;
      cursor: pointer;
    .title
      flex: 1;
      line-height: .36rem;
      span
        background: #E9EDEF;
        border-radius: 100px;
        font-size: .2rem;
        color: #8597A1;
        padding: .03rem .14rem;
        margin-left: .1rem;
    .icon-btn_icon_input:before
      margin-right: .12rem;
</style>
<style>
.studentSelectCourse .courseListWrap{
	width: 13.5rem;
	height: 100%;
	position: relative;
}
ul, li{
	list-style: none;
}
.courseListWrap .head{
	width: 13.5rem;
	height: 0.6rem;
}
.head .week{
	width: 100%;
	height: 0.6rem;
	display: flex;
	justify-content: space-around;
	align-items: center;
	padding: 0;
	margin: 0;
}
.head .week .weekItem{
	text-align: center;
	line-height: 0.6rem;
	border: 1px solid #ccc;
	width: 100%;
	height: 100%;
}
.weekItem:first-child{
	border-right: 1px solid #f3f7f9!important;
}
.weekItem:nth-child(2){
	border-left: 1px solid #f3f7f9!important;
}
.courseListWrap .left{
	height: 4rem;
	width: 1.5rem;
	display: flex;
	flex-direction: column;
}
.courseListWrap .left .morning{
	display: flex;
	width: 3rem;
	height: 2rem;
}
.morning .cellss1{
	text-align: center;
	width: 1.5rem;
	line-height: 2rem;
	height: 2rem;
	margin: 0;
	border: 1px solid #ccc;
}
.morning .cellss2{
	height: 2rem;
	width: 1.5rem;
	display: flex;
	flex-direction: column;
	margin: 0;
}
.morning .cellss2 span{
	width: 1.5rem;
	height: 1rem;
	text-align: center;
	line-height: 1rem;
	border: 1px solid #ccc;
}
.courseListWrap .courseWrap {
	position: absolute;
	top: 0.6rem;
	left: 3rem;
	width: 10.5rem;
	height: 4rem;
	display: flex;
	flex-direction: column;
}
.courseWrap .courseCell{
	width: 10.5rem;
	height: 1rem;
	display: flex;
	margin: 0;
	padding: 0;
}
.courseCell .courseInfo{
	display: flex;
	flex-direction: column;
	width: 1.5rem;
	height: 1rem;
	text-align: center;
	justify-content: center;
	align-items: center;
	border: 1px solid #ccc;
	cursor: pointer;
}
</style>




